/// <summary>
/// Root of the app.
/// </summary>

import * as React from 'react';
const styles = require('./App.css');
import { HeaderView, NavDrawerView, RightContentView } from './Components';

interface Props {

}

interface State {
    isNavDrawerCollapsed: boolean;
}

/**
 * App class is a the root component of the application. 
 * The parent of all the components shown in the portal.
 * 
 * @class App
 * @extends {React.Component<null, null>}
 */
class App extends React.Component<Props, State> {
    constructor(props: {}) {
        super(props);
        this.state = {
            isNavDrawerCollapsed : false
        };
    }

    /**
     * Callback when drawer is toggled. State is lifted up to here.
     * 
     * @param {boolean} newState 
     * 
     * @memberOf App
     */
    onNavDrawerToggled(newState: boolean) {
        this.setState({
            isNavDrawerCollapsed: !newState
        });
    }

    render() {
        return (
                <div className={styles.App}>
                    <HeaderView />
                    <NavDrawerView callBackParent={(newState: boolean) => this.onNavDrawerToggled(newState)} />
                    <RightContentView 
                        isNavDrawerCollapsed={this.state.isNavDrawerCollapsed} 
                        children={this.props.children}
                    />
                </div>
        );
    }
}

export default App ;


