/// <summary>
/// Entry to the app.
/// </summary>

import * as React from 'react';
import * as ReactDOM from 'react-dom';
import App from './App';
import './index.css';
import { Route, Router, IndexRoute, browserHistory } from 'react-router';
import RoomConfigViewStateless  from './Components/Room-Config/';
import HomeViewStateless from './Components/Home';
import UserViewStateless from './Components/Users';
import ServiceHealthViewStateless from './Components/Service-Health';
import ReportsViewStateless from './Components/Reports';

ReactDOM.render(
    (
        <Router history={browserHistory}>
            <Route path="/" component={App}>
                <IndexRoute component = {HomeViewStateless} />
                <Route path = "/RoomConfig" component = {RoomConfigViewStateless} />
                <Route path = "/User" component = {UserViewStateless} />
                <Route path = "/ServiceHealth" component = {ServiceHealthViewStateless} />
                <Route path = "/Reports" component = {ReportsViewStateless} />
            </Route>
        </Router>
    ),
    document.getElementById('root') as HTMLElement
);


