/// <summary>
/// Room table view
/// </summary>

import * as React from 'react';
import { Link } from 'office-ui-fabric-react/lib/Link';
import { dummyData } from './dummy-data';
import { DetailsList, buildColumns, IColumn, DetailsListLayoutMode } from 'office-ui-fabric-react/lib/DetailsList';

const styles = require('./index.module.css');

interface IDetailsListCustomColumnsExampleState {
    sortedItems: any[];
    columns: IColumn[];
}

function _renderItemColumn(item, index, column) {
    let fieldContent = item[column.fieldName];

    switch (column.key) {
        case 'roomName':
            return <Link src = {fieldContent} width = {50} height = {50} >{fieldContent}</Link>;

        default:
            return <span>{fieldContent}</span>;
    }
}

class RoomConfigTableView extends React.Component<{}, IDetailsListCustomColumnsExampleState> {

    constructor(props: {}) {
        super(props);

        this.state = {
            sortedItems: dummyData.data,
            columns: this._buildColumns()
        };

        this._onColumnClick = this._onColumnClick.bind(this);
    }

    /**
     * render method.
     * 
     * @returns 
     * 
     * @memberOf RoomConfigTableView
     */
    public render() {
        let { sortedItems, columns } = this.state;

        return (
            <DetailsList
                className = {styles.table}
                items = {sortedItems}
                setKey = "set"
                columns = {columns}
                layoutMode = {DetailsListLayoutMode.fixedColumns}
                onRenderItemColumn = {_renderItemColumn}
                onColumnHeaderClick = {this._onColumnClick}
                onItemInvoked = {(item, index) => alert(`Item ${item.name} at index ${index} has been invoked.`)}
            />
        );
    }

    /**
     * build column function.
     * 
     * @private
     * @returns 
     * 
     * @memberOf RoomConfigTableView
     */
    private _buildColumns() {

        let columns = buildColumns(dummyData.data);

        for (let i = 0; i < columns.length; i++ ) {
            columns[i].name = dummyData.columnHeader[i];
        }

        columns[0].maxWidth = 80;
        columns[1].maxWidth = 80;
        columns[2].maxWidth = 80;
        columns[3].maxWidth = 120;
        columns[4].maxWidth = 120;
        columns[5].maxWidth = 80;
        columns[6].maxWidth = 120;
        columns[7].maxWidth = 80;

        return columns;
    }

    /**
     * Column click function
     * 
     * @private
     * @param {any} ev 
     * @param {any} column 
     * 
     * @memberOf RoomConfigTableView
     */
    private _onColumnClick(ev, column) {

        let { sortedItems, columns } = this.state;
        let isSortedDescending = column.isSortedDescending;

        // If we've sorted this column, flip it.
        //
        if (column.isSorted) {
            isSortedDescending = !isSortedDescending;
        }

        // Sort the items.
        //
        sortedItems = sortedItems.concat([]).sort((a, b) => {
            let firstValue = a[column.fieldName];
            let secondValue = b[column.fieldName];

            if (isSortedDescending) {
                return firstValue > secondValue ? -1 : 1;
            } else {
                return firstValue > secondValue ? 1 : -1;
            }
        });

        // Reset the items and columns to match the state.
        //
        this.setState({
            sortedItems: sortedItems,
            columns: columns.map(col => {
                col.isSorted = (col.key === column.key);

                if (col.isSorted) {
                    col.isSortedDescending = isSortedDescending;
                }

                return col;
            })
        });
    }
}

export { RoomConfigTableView };


