/// <summary>
/// right content area view
/// </summary>

import * as React from 'react';
const styles = require('./index.module.css');
import { SearchBox } from 'office-ui-fabric-react/lib/SearchBox';
import { PrimaryButton, DefaultButton } from 'office-ui-fabric-react/lib/Button';
import { RoomConfigTableView } from './Room-Table';

// import { MarqueeSelection } from 'office-ui-fabric-react/lib/MarqueeSelection';
// const classNames = require('classnames');

interface Props {
}

interface State {
}

/**
 * This is the view class of content on the portal right side.
 *
 * @class RightContentView
 * @extends {React.Component<Props, State>}
 */

let _items = [
    {
        key: 'a',
        name: 'a',
        color: 'red'
    },
    {
        key: 'b',
        name: 'b',
        color: 'red'
    },
    {
        key: 'c',
        name: 'c',
        color: 'blue'
    },
    {
        key: 'd',
        name: 'd',
        color: 'blue'
    },
    {
        key: 'e',
        name: 'e',
        color: 'blue'
    }
];

class RoomConfigView extends React.Component<Props, State> {

    /**
     * Creates an instance of HomeView.
     * @param {*} props
     *
     * @memberOf RightContentView
     */
    constructor(props: any) {
        super(props);
        this.state = {
            items: _items
        };
    }

    /**
     * Render method
     *
     * @returns rendered html
     *
     * @memberOf RightContentView
     */
    render() {

        return (
            <div>
                <div className={styles.header}>
                    <span className="ms-font-xxl">Home&nbsp;</span>
                    <i className="ms-Icon ms-Icon--ChevronRight"/>
                    <span className="ms-font-xxl">&nbsp;Configuration</span>
                </div>
                <div className={styles.content}>
                    <SearchBox labelText="Enter to search for a room"/>
                    <PrimaryButton className={styles.btn} icon="Add" text="Add a room" />
                    <DefaultButton className={styles.btn} icon="Delete" text="Delete a room" />
                    <DefaultButton className={styles.btn} icon="Refresh" text="Restart room service" />
                    <RoomConfigTableView />
                </div>
            </div>

        );
    }
}

export default RoomConfigView;


