/// <summary>
/// right content area view
/// </summary>

import * as React from 'react';
import * as classNames from 'classnames';

const styles = require('./index.module.css');

interface Props {
    isNavDrawerCollapsed: boolean;
}

interface State {
}

/**
 * This is the view class of content on the portal right side.
 *
 * @class RightContentView
 * @extends {React.Component<Props, State>}
 */
class RightContentView extends React.Component<Props, State> {

    /**
     * Creates an instance of HomeView.
     * @param {*} props
     *
     * @memberOf RightContentView
     */
    constructor(props: any) {
        super(props);
    }

    /**
     * Render method
     *
     * @returns rendered html
     *
     * @memberOf RightContentView
     */
    render() {

        let rightMainContentWrapperClasses = classNames({
            [styles.rightMainContentWrapper]: true,
            [styles.expanded]: this.props.isNavDrawerCollapsed
        });

        return (
            <div className={rightMainContentWrapperClasses}>
                <div className={styles.rightMainContentContainer}>
                    {this.props.children}
                </div>
            </div>
        );
    }
}

export { RightContentView }


