/// <summary>
/// Room config view stateless.
/// </summary>

import * as React from 'react';
import RoomConfigView from './room-config-view';

/**
 * RoomConfigViewStateless class is the wrapper of RoomConfigView. 
 * 
 * @class App
 * @extends {React.Component<void, void>}
 */
class RoomConfigViewStateless extends React.Component<void, void> {

    render() {
        return (
            <RoomConfigView />
        );
    }
}

export default RoomConfigViewStateless;


