/// <summary>
/// Header view class.
/// </summary>

import * as React from 'react';
const styles = require('./index.module.css');

interface Props {
}

interface State {
}

class HeaderView extends React.Component<Props, State> {

    /**
     * Creates an instance of HeaderView.
     * @param {*} props 
     * 
     * @memberOf HeaderView
     */
    constructor(props: {}) {
      super(props);
      this.state = {};
    }

    /**
     * Render method
     * 
     * @returns rendered html
     * 
     * @memberOf HeaderView
     */
    render() {
        return (
        <div className={styles.headerWrapper}>
            <div className={`${styles.content} ms-font-xl`}>
                Apollo | Admin Center
            </div>
        </div>
        );
    }
}

export { HeaderView }


